import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import pam from './pam.jpg';
import bryan from './bryan.jpg';


class Card extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      words: "Hello, my love...",
      thingsToSay: ["you are the best","i love you so much", "did it hurt? when you fell from heaven..."],
      numberOn: 0,
      intensityNo: 1
    };
  }

  nextSlide() {
    clearTimeout(window.timer1);
    clearTimeout(window.timer2);
    document.getElementById("scrollingContent").style.marginTop = "600px";
    this.scrollWords(this.state.thingsToSay[this.state.numberOn]);
    this.state.numberOn++;

    if (this.state.numberOn > this.state.thingsToSay.length) {
      // end scrolling things
      document.getElementById("scrollingContent").style.display = "none";
      clearTimeout(window.timer1);
      clearTimeout(window.timer2);
      // begin intensity
      document.getElementById("intensity").style.display = "block";
      this.beginIntensity();
    }
  }

  Button (props) {
    return (
      <div id="button" className="button" onClick={this.playCard.bind(this)}>
        Play Card
      </div>
    );
  }

  RestartButton (props) {
    return (
      <div id="button" className="button" onClick={this.restart.bind(this)}>
        Replay
      </div>
    );
  }

  scrollingContent(props) {
    return (
      <div id="scrollingContent">
        {this.words()}
      </div>
    );
  }

  render() {
    return (
      <div className="outerContainer">
        <div className="container">
          {this.Button()}
          <div id="card">
            {this.scrollingContent()}
            {this.intensity()}
            {this.finale()}
          </div>
        </div>
      </div>
    );
  }
  
  words() {
    return (this.state.words);
  }

  playCard() {
    // play card here
    console.log("playCard()");
    document.getElementById("card").style.display = "block";
    document.getElementById("button").style.display = "none";
    document.getElementById("scrollingContent").style.marginTop = "600px";
    this.scrollWords("hey, guess what?");
    return null;
  }


  scrollWords(wordstring) {
    this.setState((state) => {return {words: wordstring}});
    window.timer1 = setTimeout(this.timer1.bind(this), 10, this);
  }

  timer1(e) {
    console.log("timer1", this.state);
    let $1 = document.getElementById("scrollingContent");
    let pTop = parseInt($1.style.marginTop);
    pTop -= 5;
    if (pTop <= 0) {
      clearTimeout(window.timer1);
      pTop = 0;
      $1.style.marginTop = pTop + "px";
      document.getElementById("scrollingContent").style.opacity = 1;
      this.fadeOut(document.getElementById("scrollingContent"));
      
      return;
    }
    $1.style.marginTop = pTop + "px";
    window.timer1 = setTimeout(this.timer1.bind(this), 10, this);
  }

  fadeOut(element) {
    let op = element.style.opacity;
    console.log(op);
    op -= .1;
    if (op <= 0) {
      op = 0;
      element.style.opacity = 1;
      this.nextSlide();
      return;
    }
    console.log(op);
    element.style.opacity = op;
    window.timer2 = setTimeout(this.fadeOut.bind(this, element), 100, this, element);
  }
  intensity(props) {
    return (
      <div id="intensity" style={{display: "none"}}>
        <div id="scars1">Do you even realize Pam,</div>
        <div id="scars2">How much you mean to me?</div>
        <div id="scars3">You've opened up my eyes</div>
        <div id="scars4">To my true identity</div>
        <div id="scars5">There must have been another force</div>
        <div id="scars6">Like the Holy Trinity</div>
        <div id="scars7">Because baby, I Love You </div>
        <div id="scars8">and I wanted you to see...</div>
      </div>
    );
  }

  beginIntensity() {
    console.log("Beginning intensity...");
    for (var i = 1; i < 9; i++) {
      document.getElementById("scars" + i).style.opacity = 0;
    }
    this.intensify();
  }

  intensify(e) {
    console.log("intensifying...");

    let elem = document.getElementById("scars" + this.state.intensityNo);
// get opacity
let oldOpacity = parseFloat(elem.style.opacity);
let newOpacity = oldOpacity + .1;
console.log("old: ", oldOpacity, "new:", newOpacity)

// set opacity
elem.style.opacity = newOpacity;


    
    if (document.getElementById("scars" + this.state.intensityNo).style.opacity == "1") {
      console.log(this.state.intensityNo);
      clearTimeout(window.timer3);
      this.setState({intensityNo: this.state.intensityNo + 1});
      if (this.state.intensityNo > 8){

        this.showFinale();
        return;
      }
      
    }
    window.timer3 = setTimeout(this.intensify.bind(this), 100, this);
  }


  showFinale() {
    document.getElementById("intensity").style.display = "none";
    document.getElementById("finale").style.display = "block";
  }
  
  finale(props) {
    return (
      <div id="finale" style={{display: "none"}}>
        <img alt="bryan<3" src={bryan} className="img img_left"></img>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        +
        <br/>
        <img alt="PAM!<33" src={pam} className="img img_right"></img>
        <br/>
        <span class="clear">
          We belong together!
          <br/>
          I LOVE YOU!
        </span>
        {this.RestartButton()}
      </div>
    );
  }

  restart() {
    document.getElementById("finale").style.display = "none";
    document.getElementById("scrollingContent").style.display = "block";
    this.playCard();
  }



}


